<template>
	<section class="portfolio-works">
		  <div class="section-title">
			  <h2 class="section-title__text">Мої роботи</h2>
		  </div>
		  <!-- /.section-title -->
		  <div class="section-subtitle">
			  <div class="section-subtitle__text">На даній сторінці зібрані всі мої проекти. Ви можете переглянути деталі кожного проекту окремо або ж одразу подивитись на готовий результат на сторінці проекту.</div>
			  <!-- /.section-subtitle__text -->
		  </div>
		  <!-- /.section-subtitle -->
		  <div class="empty-list text-center" v-if='works.length === 0'>Сталася помилка при отриманні даних! Будь ласка перевірте з'єднання з мережею.</div>
		 <div class="portfolio-filter">
			<ul class="portfolio-filter__list">
			  <li class="portfolio-filter__item" :class='{active: activeAll}' @click='filterWorks()'>Всі</li>
			  <!-- /.portfolio-filter__item -->
			  <li class="portfolio-filter__item" :class='{active: activeSites}' @click='filterWorks("sites")'>Сайти</li>
			  <!-- /.portfolio-filter__item -->
			  <li class="portfolio-filter__item" :class='{active: activePersonal}' @click='filterWorks("personal")'>Особисті проекти</li>
			  <!-- /.portfolio-filter__item -->
			</ul>
			<!-- /.portfilio-filter__list -->
		 </div>
		  <!-- /.portfolio-filter -->
	  		<transition-group tag='div' class='row' name='slide-fade' :key=''>
	  			
				<div class="col-12 col-lg-6 col-xl-4" v-for='work in filtered' :key='work.id'>
					<div class="flip portfolio-item">
						<div class="edit" v-if='User'>
							<router-link :to="{ name: 'edit_work', params: { title: $_friendlyUrl(work.title), id: work.id }}">
	 				  			<img src="../blog/icons/edit-regular.svg" alt="Edit">
							</router-link>
							
						</div>
					    <div class="front" :style="{'background-image': `url(${work.img})`}">
					       <h5 class="portfolio-item__title">{{work.title}}</h5>
					    </div>
					    <div class="back" :style="{'background-image': `url(${work.img})`}">
					    	<div class="portfolio-item__links">
								<router-link title='Дивитись деталі' class="portfolio-item__link" tag='span' :to="{ name: 'single-work', params: { title: $_friendlyUrl(work.title),id: work.id }}">
								  Дивитись деталі
								</router-link>
								<a title="Переглянути проект" :href="work.url" class="portfolio-item__link">
								 Переглянути проект
								</a>
							</div>
					    </div>
					</div>
				</div>
			</transition-group>
		  <div class="none" v-if='filtered.length === 0'>Робіт не знайдено!</div>
	</section>
	<!-- /.portfolio-works -->
	 
</template>
<script>
	export default {
		name: 'portfolioWorks',
		created() {
			this.filtered = this.works;
		},
	  	data () {
			return {
				works: [],
				filtered: [],
				activeSites: false,
				activePersonal: false,
				activeAll: true
			}
	 	},
	  	firestore () {
		 	return {
		   		works: this.$_db.collection('works').orderBy('createdAt')
		 	}
	  	},
	  	methods: {
			filterWorks(cat) {
		  		this.filtered = [];
		  		if(cat) {
					this.clearActive();

					this.filtered = this.works.filter(item => item.cat === cat);

					cat == 'sites' ? this.activeSites = true : this.activePersonal = true;
		  		} else {
					this.clearActive();
					this.filtered = this.works;
					this.activeAll = true;
		  		}
			},
			clearActive() {
		  		this.activeAll = this.activeSites = this.activePersonal = false;
			},
	  	},
  	   	computed: {
  			User() {
  				return this.$store.getters.User;
  			}
  		},
	}
</script>
<style>
  .list-complete-enter, .list-complete-leave-to {
	opacity: 0;
	transform: translateY(30px);
  }
  .list-complete-leave-active {
	position: absolute;
  }
</style>